@media ( max-width: 767px ) {
	.menu {
		position: fixed;
		top: 0;
		left: -100%;
		width: 50%;
		height: 100vh;
		padding: 20px 15px;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		background-color: rgba(0, 0, 0, .8);
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			padding-right: 0;
			border: none; }
		&_link {
			font-size: 20px; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		width: 26px;
		height: 12px;
		span {
			display: block;
			height: 2px;
			width: 100%;
			background-color: #fff;
			transition: 0.5s all;
			margin-bottom: 2px; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; }
	.xs-visible {
		display: block; }
