		&_btn {
			display: block;
			margin: 0 auto;
			width: 200px;
			height: 35px;
			color: #ffffff;
			font-size: 15px;
			font-weight: 400;
			text-align: center;
			text-transform: uppercase; } }
	.promo {
		&_header {
